.btnPlay {
  display: block;
  width: 254px;
  eight: 70px;
  padding: 0 70px;
  margin-top: 140px;
  margin-left: 50%;
  border: 1px solid #2297f1;
  border-radius: 20px;
  color: #2297f1;
  font-family: 'microsoft yahei';
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.7);
  font-size: 34px;
  text-align: center;
  text-decoration: none;
  line-height: 65px;
  letter-spacing: 1.3px;
  opacity: 0;
  transition: color 0.3s;
  transform: translate3d(-50%, 0, 0);
  animation: btnPlayAnimate 1.3s 2.8s forwards; }
  .btnPlay:hover {
    color: #fff;
    border: 1px solid transparent; }
    .btnPlay:hover::before {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); }
  .btnPlay::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #2297f1;
    color: #fff;
    border-radius: 20px;
    z-index: -1;
    opacity: 0;
    transform: scale3d(0.5, 1, 1);
    transition: transform 0.4s, opacity 0.4s;
    -webkit-transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
    transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1); }
  .btnPlay::after {
    content: '';
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 20px;
    animation: btnPlayAnimateAfter 1500ms 3.2s infinite; }

@keyframes btnPlayAnimateAfter {
  50% {
    box-shadow: 0 0 15px #2297f1; }
  100% {
    box-shadow: none; } }

@keyframes btnPlayAnimate {
  to {
    opacity: 1;
    margin-top: 70px; } }

